const appInfo = JSON.parse(sessionStorage.getItem("WYU_SHOP_INFO"));
const app = {
    state: {
        appInfo:appInfo ? appInfo : [],
    },
    mutations: {
        SET_APP_INFO: (state, info) => {
            state.appInfo = info;
            sessionStorage.setItem("WYU_SHOP_INFO",JSON.stringify(info));
        },
    },
    actions: {
        // 用户名登录
        setAppInfo({commit}, appInfo) {
            return new Promise((resolve) => {
                commit('SET_APP_INFO', appInfo);
                resolve();
            });
        },
        // 登出
        LogOut({commit}) {
            return new Promise((resolve) => {
                commit('SET_APP_INFO', []);
                resolve();
            });
        },
    }
};
export default app
