import Vue from 'vue'
import Router from 'vue-router'
import menus from './menu';
Router.prototype.goBack = function () {
    this.isBack = true;
    window.history.go(-1);
};
Vue.use(Router);
const router = new Router({
    base: process.env.BASE_URL,
    routes: [
        //登录小程序
        {
            path: '/',
            name: 'applogin',
            component: () => import( './pages/Login.vue'),
            meta: {title: '登录小程序'},
        },
        {
            path: '/loginapp/:id/:token',
            name: 'loginapp',
            component: () => import( './pages/LoginApp.vue'),
            meta: {title: '登录小程序'},
        },
        {
            path: '/frame',
            name: 'adminframe',
            component: () => import( './pages/Frame.vue'),
            meta: {title: '后台框架'},
            children: menus.menuRouter
        },

        {
            path: '/design/:pageid',
            name: 'designpage',
            component: () => import( './pages/setting/style/Design.vue'),
            meta: {title: '可视化制作小程序'},
        },

        //未找到定义的路由 必须放在最底部
         {
             path: '*',
             component: () => import( './pages/Nopage.vue'),
             meta: {
                 title: '你好像迷路了(｀・ω・´)',
             },
         },
    ]
});


export default router;
