import router from './router'
import store from './store/'  // token  和 权限
import NProgress from 'nprogress' //进度条
import 'nprogress/nprogress.css'//进度条
const whiteList = ['loginapp','applogin']; //不验证权限白名单
const title = '后台管理中心';
NProgress.configure({showSpinner: false});
/*在跳转之前执行*/
router.beforeEach((to, from, next) => {
    NProgress.start();
    let token = store.getters.appToken;
    let appInfo   = store.getters.appInfo;
     if (to.meta.title) {
         document.title = to.meta.title + '-' + title
    }
    next();
    if(token){
    
        let is_auth = true;
            if(is_auth){
                next();
                NProgress.done();
            }else{
                next(`/noauth`);
                NProgress.done();
            }
    }else {
        if (whiteList.indexOf(to.name) !== -1) { // 在免登录白名单，直接进入
            next();
            NProgress.done();
        } else {
            next(`/`); // 否则全部重定向到登录页
            NProgress.done();
        }
    }
});
/*在跳转之后判断*/
router.afterEach(() => {
    NProgress.done();
});
